<template>
  <nav-bar></nav-bar>
  <component
    :is="currentPage"
    :recipeData="recipeData"
    :recipeTitle="currentRecipe"
    @selectRecipe="changeCurrentRecipe"
  ></component>
  <the-footer></the-footer>
</template>
<script>
import NavBar from "./components/navigation/NavBarBlock.vue";
import TheFooter from "./components/footer/footerBlock";
import RecipeList from "./components/RecipeList/RecipeListBlock";
import RecipeCard from "./components/RecipeCard/RecipeCardBlock";
export default {
  name: "App",
  components: {
    NavBar,
    RecipeList,
    RecipeCard,
    TheFooter,
  },
  data() {
    return {
      recipeData: [],
      currentPage: "recipe-list",
      currentRecipe: "Basil and pesto Hummus",
    };
  },

  methods: {
    // setFilter(filterOption, filterInput) {
    //   console.log("done");
    //   this.filterOption = filterOption;
    //   this.filterInput = filterInput;
    //   console.log(this.filterOption);
    // },
    changeCurrentRecipe(recipe){
      this.currentRecipe=recipe
      this.currentPage = "recipe-card"
      console.log(recipe);
    },
    fetchData() {
      fetch("https://recipes-36a72-default-rtdb.firebaseio.com/.json").then(
        (response) => {
          if (!response.ok) {
            console.log("error - data not recieved via fetch");
          } else {
            response.json().then((data) => {
              console.log("fetched in recipe");
              console.log(data);
              return (this.recipeData = data);
            });
          }
        }
      );
    },
  },
  created() {
    this.fetchData();
  },
  provide() {
    return {};
  },
};
</script>

