<template>
    <main>
    
        <section>
            <img src="" alt="image not found">
            <h1>{{recipe.title}}</h1>
            <p>{{recipe.description}}</p>
        </section>
        <section>
            <ul></ul>
            <li v-for="ing in ingredients" :key="ing">
                {{ing}}
            </li>
        </section>
    </main>
</template>
<script>
export default {
    props:{
        recipeData:{
            type:Array,
            required:true
        },
        recipeTitle:{
            type:String,
              required:true
        }
    },
computed:{
    recipe(){
        let recipe = this.recipeData.find(recipe=>recipe.title.toLowerCase()==this.recipeTitle.toLowerCase())
        return recipe
    }
},

}
</script>
<style lang="scss" scoped>
main{
    margin-top:5rem;
}
</style>