<template>
  <li>
    <img v-if="imagePresent" :src="imageCompute()" alt="image not available" />
    <img v-else src="@/assets/img/fostrLogo.png" alt="Image not Loaded" />
    <h1>{{ title }}</h1>
    <h3>{{ imageCompute }}</h3>
    <section>
      <h5>{{ prepTime }}</h5>
      <h5>{{ cookTime }}</h5>
    </section>
  </li>
</template>
<script>
export default {
  data() {
    return {
      image: "",
      imagePresent: true,
    };
  },
  props: {
    title: {
      type: String,
      required: true,
    },
    prepTime: {
      type: String,
      default: "N/A",
    },
    cookTime: {
      type: String,
      default: "N/A",
    },
    id: {},
  },
  methods: {
    imageCompute() {
      try {
        let image = require(`@/assets/img/${this.title.replace(
          /\s/g,
          ""
        )}.jpg`);
        return image;
      } catch (e) {
        console.log(e);
        return (this.imagePresent = false);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
li {
  flex: 0 0 40%;
  margin: 2rem;
  border: 1rem solid black;
  &:hover {
    background: red;
    cursor: pointer;
  }
}
img {
  width: 50%;
}
</style>
