<template>
  <hero></hero>
  <main>
    <ul>
      <recipe-list-item
        v-for="recipe in filteredData"
        @click="log(recipe.title)"
        :key="recipe.title"
        :id="recipe.title"
        :title="recipe.title"
        :prepTime="recipe.prep_time_min"
        :cookTime="recipe.cook_time_min"
      ></recipe-list-item>
    </ul>
  </main>
</template>
<script>
import Hero from "../header/Hero";
import RecipeListItem from "./recipeListItem";
export default {
  components: {
    Hero,
    RecipeListItem,
  },
  props: {
    recipeData: {
      Type: Array,
    },
  },
  data() {
    return {
      filter: ["", "title"],
    };
  },
  methods: {
    log(id) {
     this.$emit('select-recipe',id)
console.log(id + ' log');
    },
  },
  computed: {
    filteredData() {
      if (this.filter[0] !== "") {
        let filtered = this.recipeData.filter(
          (recipe) =>
            recipe[this.filter[1].toLowerCase()] ===
            this.filter[0].toLowerCase()
        );
        return filtered;
      } else {
        return this.recipeData;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
main {
  background: url("../../assets/img/marbel.jpg");
  background-position: center;
  background-size: fill;
}
ul {
  list-style: none;
  flex-wrap: wrap;
  display: flex;
  align-items: stretch;
}
</style>
