/* eslint-disable prettier/prettier */
<template>
  <nav :class="{ black: pastHero }">
    <a href="/">
      <img src="../../assets/img/fostrLogo.png" alt="" />
    </a>
    <form @submit.prevent="$emit('log',filterOption,filterInput
    )">
      <input name="search" type="text" placeholder="search" v-model="filterInput" />
      <select name="filter" id="filter" v-model="filterOption" >
        <option value="" selected disabled>filter</option>
        <option value="title" selected>Title</option>
        <option value="ingredient">Ingredient</option>
        <option value="tags">Tags</option>
      </select>
    </form>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      filterInput:'',
      filterOption:'',
      pastHero: false,
    };
  },
  created() {
    window.addEventListener("scroll", this.changeColour);
  },
  methods: {
    log(){
      // console.log(this.filterOption)
      // console.log(this.filterInput)
      // this.$emit(this.filterOption,this.filterInput)
    },
    changeColour() {
      this.scrollPosition = window.scrollY;
      this.screenHeight = window.innerHeight * 0.75;
      this.scrollPosition > this.screenHeight
        ? (this.pastHero = true)
        : (this.pastHero = false);
    },
  },
};
</script>
<style lang="scss" scoped>
nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 0.75rem 1.5rem;
  display: flex;
  align-items: center;
  // background: rgba(0, 0, 0, 0.2);
  transition: all 0.4s;
}
img {
  max-height: 100%;
}
form {
  margin-left: auto;
}
input {
  width: 30ch;
  border-radius: 10px 0px 0px 10px;
  border-right: unset !important;
}
select {
  border-radius: 0 10px 10px 0;
  &:focus {
    border-radius: 0 10px 0 0;
  }
}
select,
option,
input {
  height: 2rem;
  padding: 2px 10px;
  background: black;
  border: 2px white solid;
  color: rgb(170, 170, 170);
  outline: none;
  &:focus {
    color: white;
    &::placeholder {
      color: white;
    }
  }
}

a {
  height: 4rem;
}
.black {
  background: black;
}
</style>
